
.content{
  width: 60%;
  padding: 3rem;
  border:1px solid #ddd;
  background:#f9fafc;
}
ul li{
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 2px solid #c0ccda;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row; 
  -webkit-box-pack: start; 
      -ms-flex-pack: start; 
          justify-content: flex-start;
  padding: 1rem 0;
}
ul li img{
  width: 4rem;
  height: 4rem;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  float: left;
  margin-right: 2rem;
}
ul li .right{
  /*float: left;*/
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  width: 100%;
}
ul li .right a{
  font-size: 26px;
  color: black;
}
ul li .right a:visited{
  color: gray;
}
ul li .right span{
  font-size: 17px;
  color: #8492a6;
  margin-right: 2rem
}





